<template>
  
  <div class="result" v-if="winner_votes!=0">
    <div>
      <h1>And the winner is... {{ winner }}</h1>
      <p><strong>with {{ winner_votes }} electoral votes</strong></p>
    </div>
    <div>
      <p>States assumed to be won by Biden: {{ biden_assumed_states.join(", ") }}</p>
      <p>Swing states calculated to be won by Biden: {{ biden_states.join(", ") }}</p>
    </div>
    <div>
      <p>States assumed to be won by Trump: {{ trump_assumed_states.join(", ") }}</p>
      <p>Swing states calculated to be won by Trump: {{ trump_states.join(", ") }}</p>
    </div>
  </div>
  <hr>
  <div class="data_manip">
    <p>Weights to adjust outcome, as a decimal percentage:</p>
    <form id="data_entry" @submit.prevent="sim_elect">
      <div>
        <label for="rep_for_trump" class="form-control">Republicans voting Trump: </label>
        <input id="rep_for_trump" type="text" v-model="r_for_trump">
      </div>
      <div>
        <label for="dem_for_biden">Democrats voting Biden: </label>
        <input id="dem_for_biden" type="text" v-model="d_for_biden">
      </div>
      <div>
        <label for="indep_for_biden">Independents voting Biden: </label>
        <input id="indep_for_biden" type="text" v-model="i_for_biden">
      </div>
      <div>
        <label for="rep_early">Republicans voting early: </label>
        <input id="rep_early" type="text" v-model="r_early">
      </div>
      <div>
        <label for="dem_early">Democrats voting early: </label>
        <input id="dem_early" type="text" v-model="d_early">
      </div>
      <div>
        <label for="indep_early">Independents voting early: </label>
        <input id="indep_early" type="text" v-model="i_early">
      </div>
    
       <button>Get election results</button>
    </form>
  </div>
  <div>
    <p>Early voting data is not necessarily an accurate predictor of who will win the election.</p>
    <p>This site is based on the ideas outlined in <a href="https://docs.google.com/document/d/16bjqHp-lTT7lG_3LEVZ8zy8k4Cx2PCWLGhyfYDfj-wc/edit?usp=sharing" target="_blank">this document</a>, which also explains the math behind the calculations.</p>
    <p>Sources for default 
    <a href="https://news.gallup.com/poll/203198/presidential-approval-ratings-donald-trump.aspx">votes for a candidate by party affiliation</a>,
    <a href="https://www.cbsnews.com/news/biden-trump-election-day-early-vote-opinion-poll-11-01-2020/" target="_blank">R and D early voting percent</a> 
    and
    <a href="https://morningconsult.com/2020/10/15/democrats-preferred-to-vote-by-mail-but-as-election-day-nears-more-say-theyll-head-to-the-polls/" target="_blank">I early voting percent</a>, with more sources with similar numbers in above document.</p>
    <p>All election data comes from 
    <a href="https://electproject.github.io/Early-Vote-2020G/index.html" target="_blank">electproject.github.io</a>
    and 
    <a href="https://targetearly.targetsmart.com/" target="_blank">targetearly.targetsmart.com</a>,
    and is up to date as of 11/01 11PM PST.
    </p>
  </div>
</template>

<script>

const states = require('../states.json')
const vote_data = require('../vote_data.json')

export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      r_for_trump: .95,
      d_for_biden: .97,
      i_for_biden: .59,
      r_early: .32,
      d_early: .66,
      i_early: .48,
      winner: "",
      winner_votes: 0,
      trump_states: [],
      trump_assumed_states: [],
      biden_states: [],
      biden_assumed_states: [],
    }
  },
  methods: {
    sim_elect : function() {
      var biden_votes = 0;
      this.trump_states = [];
      this.biden_states = [];
      this.trump_assumed_states = [];
      this.biden_assumed_states = [];
      Object.keys(states).forEach(function(key){
        if(states[key]["est"] == "D") {
          biden_votes += parseInt(states[key]["e_votes"]);
          this.biden_assumed_states.push(key);
        }
        else if(states[key]["est"] == "N") {
          var svd = vote_data[key];
          var r_votes = svd["rep"]/this.r_early;
          var d_votes = svd["dem"]/this.d_early;
          var i_votes = svd["indep"]/this.i_early;
          var trump = r_votes*this.r_for_trump + d_votes*(1-this.d_for_biden) + i_votes*(1-this.i_for_biden);
          var biden = r_votes*(1-this.r_for_trump) + d_votes*this.d_for_biden + i_votes*this.i_for_biden;
          if(biden>trump) {
             biden_votes += parseInt(states[key]["e_votes"]);
             this.biden_states.push(key);
          }
          else {
            this.trump_states.push(key);
          }
        }
        else {
          this.trump_assumed_states.push(key);
        }
        }, this);
      console.log(biden_votes);
      this.winner = biden_votes >= 270 ? "Biden" : "Trump";
      if(this.winner === "Biden") {
        this.winner_votes = biden_votes;
      }
      else {
        this.winner_votes = 538 - biden_votes;
      }
    }
  }
}
</script>

